.epi-editmode {
    .reveal-item {
        opacity: 1 !important;
    }
    .share-this-page-block,
    .shared-block-container-full,
    .wide-rich-text-container,
    .wide-carousel-block,
    .related-experts-block {
        padding-right: 0 !important;
        padding-left: 0 !important;
        margin-left: 0 !important;
        margin-right: 0 !important;
    }
    .col-md-9 {
        @media (min-width: 1024px) {
            .stpb-container {
                background: #ffffff !important;
            }
            .share-this-page-block {
                .share-this-page-block-container {
                    margin-left: 0 !important;
                }
            }
            .sbcf-container {
                background: #4F2D7F !important;
            }
            .shared-block-container-full {
                .shared-subscribe-block--dark {
                    margin-left: 0 !important;
                }
            }
        }
    }
    *+.withSecondaryNav {
        .col-md-9 {
            .share-this-page-block {
                overflow: visible;
                margin-bottom: 0;
                @media (max-width: 767px) {
                    margin-bottom: 0;
                }
            }
            div div:last-of-type .stpb-container {
                margin-top: 0;
                margin-bottom: 0 !important;
            }
        }
    }
    .person-info-block {
        .person-adress,
        .person-phone {
            margin-top: 0 !important;
        }
    }
    .extended-banner-block {
        .container {
            h1.title {
                margin-top: 0;
            }
            span.category {
                font-size: 1.4rem !important;
                max-width: 100%;
                .epi-editContainer {
                    font-size: 1.4rem !important;
                    max-width: 100%;
                }
            }
            .epi-editContainer {
                font-size: 1.6rem !important;
                margin: 0 0 15px 0 !important;
                line-height: 150% !important;
                @media (min-width: 1300px) {
                    max-width: 51%;
                }
                @media (max-width: 1299px) and (min-width: 1024px) {
                    max-width: 52%;
                }
            }
        }
    }
    &>.main>div>.EPiServerForms,
    &>.main>p {
        padding: 0 32px;
    }
    .main.op-0 {
        visibility: visible;
        opacity: 1;
    }
    .epi-editContainer [data-epi-block-id] {
        &:after {
            display: table;
            content: " ";
            clear: both;
        }
    }
    .EPiServerForms .Form__Element .Form__Element__Caption {
        display: block;
    }
}

[data-epi-property-name="Banner"]+.content.container {
    margin-top: 128px;
}